.gpt3__features{
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 900px) {
    flex-direction: column;
  }
}
.gpt3__features-information{
  display: flex;

  flex-direction: column;
  h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 390px;
  }
  p{
    font-family: var(--font-family);
    color: var(--color-subtext);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    margin-top: 2rem;
  }
}
.gpt3__features-improving{
  justify-content: flex-end;
}