.gpt3__blog{

}
.gpt3__blog-header{
  h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    max-width: 700px;
  }
  margin-bottom: 7rem;
}

.gpt3__blog__content{
  display: flex;
  @media screen and (max-width: 1000px) {
    flex-direction: column-reverse;
  }
}
.gpt3__blog__content-groupA{
  flex: 1.5;
  margin-right: 3rem;
  @media screen and (max-width: 1000px) {
    margin-top: 3rem;
  }
  @media screen and (max-width: 500px) {
    margin-top: 1rem;
  }
}
.gpt3__blog__content-groupB{
  flex: 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  @media screen and (max-width: 500px) {
    gap: 1rem;
  }
}