.gpt3__whatGPT3{
  padding: 3.5rem;
  /* ff 3.6+ */
  background:-moz-radial-gradient(ellipse at 100% 100%, rgba(4, 80, 163, 1) 0%, rgba(1, 37, 73, 1) 100%);

  /* safari 5.1+,chrome 10+ */
  background:-webkit-radial-gradient(ellipse at 100% 100%, rgba(4, 80, 163, 1) 0%, rgba(1, 37, 73, 1) 100%);

  /* opera 11.10+ */
  background:-o-radial-gradient(ellipse at 100% 100%, rgba(4, 80, 163, 1) 0%, rgba(1, 37, 73, 1) 100%);

  /* ie 10+ */
  background:-ms-radial-gradient(ellipse at 100% 100%, rgba(4, 80, 163, 1) 0%, rgba(1, 37, 73, 1) 100%);

  /* global 92%+ browsers support */
  background:radial-gradient(ellipse at 100% 100%, rgba(4, 80, 163, 1) 0%, rgba(1, 37, 73, 1) 100%);
}
.gpt3_whatGPT3-information{

  .gpt3__feature{
    p{
      max-width: unset;
    }
  }
  @media screen and (max-width: 850px) {
    flex-direction: column;
  }
}
.gpt3__whatGPT3-heading{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5rem 0;
  h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
  }
  p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
  }
  @media screen and (max-width: 850px) {
    flex-direction: column;
    p{
      margin-top: 2rem;
    }
  }
}
.gpt3__whatGPT3-blogs{
  display: flex;
  .gpt3__feature{
    flex-direction: column;
  }
  @media screen and (max-width: 850px) {
    flex-direction: column;
  }
}