.gpt3__article{
  width: 100%;
  height: 100%;
  background-color: var(--color-footer);
  display: flex;
  flex-direction: column;
}
.gpt3__article-image{
  width: 100%;
  height: 100%;
  background-color: var(--color-footer);
  img{
    width: 100%;
    height: 100%;
  }
}
.gpt3__article__text{
  padding: 1.5rem 1rem;

  &> div{
    p{
      font-family: var(--font-family);
      color: #fff;
      font-weight: 700;
      font-size: 11.649px;
      line-height: 35px;
    }
    h2{
      font-family: var(--font-family);
      color: #fff;

      font-weight: 800;
      font-size: 25.11px;
      line-height: 30px;
      margin-bottom: 5rem;
      cursor: pointer;
    }
  }
  & > p{
    font-family: var(--font-family);
    color: #fff;
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
    cursor: pointer;
  }
}