.gpt3__footer {
  background-color: var(--color-footer);
  position: relative;
}

.gpt3__footer-heading {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}
.gpt3__footer-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
}
@media screen and (max-width: 1000px) {
  .gpt3__footer-heading h1 {
    font-size: 40px;
    line-height: 60px;
  }
}

.gpt3__footer-request_button {
  margin: 4rem auto 12rem;
  width: 218px;
  height: 65px;
}
.gpt3__footer-request_button input {
  width: 100%;
  height: 100%;
  background-color: unset;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  border: 1px solid #fff;
  cursor: pointer;
}

.gpt3__footer-content {
  color: white;
  font-family: var(--font-family);
  font-size: 12px;
  display: flex;
  justify-content: space-between;
}
.gpt3__footer-content ul {
  list-style: none;
}
@media screen and (max-width: 900px) {
  .gpt3__footer-content {
    flex-direction: column;
    text-align: center;
  }
  .gpt3__footer-content ul {
    margin-top: 3rem;
  }
}

.gpt3__footer-content_logo img {
  margin-bottom: 2rem;
}

.gpt3__footer-content_links .gpt3__footer-content_links_description {
  font-size: 14px;
  cursor: text;
}
.gpt3__footer-content_links li {
  margin-bottom: 2rem;
  cursor: pointer;
}

.gpt3__footer-rights {
  position: absolute;
  color: #fff;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  bottom: 27px;
  left: 50%;
  transform: translateX(-50%);
}

