.gpt3__feature {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.gpt3__feature h2 {
  max-width: 200px;
  width: 100%;
  color: #fff;
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  flex-wrap: wrap;
}
.gpt3__feature p {
  max-width: 500px;
  width: 100%;
  color: var(--color-text);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
}
@media screen and (max-width: 850px) {
  .gpt3__feature {
    flex-direction: column;
  }
}

.gradient-line {
  height: 3px;
  width: 38px;
  display: block;
  background: var(--gradient-bar);
  margin-bottom: 0.5rem;
}

