.gpt3__header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 1300px) {
  .gpt3__header {
    flex-direction: column;
  }
}

.gpt3__header-content {
  flex: 1;
}
.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}
.gpt3__header-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);
  margin: 2rem 0;
}

.gpt3__header-content__input input[type=email] {
  width: 72%;
  height: 73px;
  background: #052D56;
  border-radius: 5px 0 0 5px;
  border: none;
  outline: none;
  padding: 1.5rem 2rem;
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: 400;
  color: #3D6184;
}
.gpt3__header-content__input input[type=button] {
  height: 73px;
  width: 28%;
  background-color: #FF4820;
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  border: none;
  outline: none;
}
@media screen and (max-width: 600px) {
  .gpt3__header-content__input input[type=email] {
    font-size: 15px;
  }
  .gpt3__header-content__input input[type=button] {
    font-size: 15px;
  }
}

.gpt3__header-content__people {
  display: flex;
  align-items: center;
  color: #fff;
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 500;
  margin-top: 2rem;
}
.gpt3__header-content__people img {
  margin-right: 1rem;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.gpt3__header-image img {
  width: 100%;
  height: 100%;
  max-width: 667.41px;
  max-height: 701.59px;
}

