.gpt3__possibility{
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: 1000px) {
    flex-direction: column;
  }
}
.gpt3__possibility-image{
  flex: 1;
  img{
    width: 100%;
    height: 100%;
    max-width: 546px;
    max-height: 607.53px;
  }
}
.gpt3__possibility__content{
  padding: 1rem;
  flex: 2;
  max-width: 700px;

  h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
  }
  p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin: 1rem 0;
  }
  .gpt3__possibility__content-top_subtext{
    color: #71E5FF ;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
  }
  .gpt3__possibility__content-bottom_subtext{
    color: var(--color-subtext) ;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
  }
}

