.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}
.gpt3__navbar p {
  color: white;
  font-weight: 500;
  font-size: 18px;
  font-family: var(--font-family);
  cursor: pointer;
  line-height: 25px;
  margin: 0 1rem;
  white-space: nowrap;
}
@media screen and (max-width: 800px) {
  .gpt3__navbar {
    padding: 1rem 2rem;
  }
}

.gpt3__navbar-links {
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__navbar-links_logo {
  margin-right: 2rem;
}
.gpt3__navbar-links_logo img {
  width: 62px;
  height: 16px;
}

.gpt3__navbar-links_container {
  display: flex;
}
@media screen and (max-width: 1100px) {
  .gpt3__navbar-links_container {
    display: none;
  }
}

.gpt3__navbar-sign {
  display: flex;
}
.gpt3__navbar-sign p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 500;
  font-size: 18px;
  font-family: var(--font-family);
  width: 152px;
  height: 58px;
  border-radius: 5px;
  cursor: pointer;
  white-space: nowrap;
}
.gpt3__navbar-sign .gpt3__navbar-sign_up {
  background-color: #FF4820;
}
@media screen and (max-width: 800px) {
  .gpt3__navbar-sign {
    display: none;
  }
}

.gpt3__navbar-menu_btn {
  display: none;
}
@media screen and (max-width: 1100px) {
  .gpt3__navbar-menu_btn {
    display: block;
  }
}

.gpt3__navbar__menu-container {
  text-align: right;
  position: absolute;
  right: 40px;
  top: 100px;
  padding: 2rem;
  border-radius: 10px;
  background-color: #021B33;
}
.gpt3__navbar__menu-container p {
  margin: 1rem 0;
}
.gpt3__navbar__menu-container .gpt3__navbar__menu-sign {
  display: none;
}
@media screen and (max-width: 800px) {
  .gpt3__navbar__menu-container .gpt3__navbar__menu-sign {
    display: block;
  }
}
.gpt3__navbar__menu-container .gpt3__navbar-sign_up {
  background-color: #FF4820;
  padding: 0.5rem 1rem;
  border-radius: 5px;
}

