.gpt3__CTA{
  background: var(--gradient-bar);
  border-radius: 10px;
  padding: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: 1000px) {
    flex-direction: column;
    .gpt3__CTA-text {
      text-align: center;
    margin-bottom: 3rem;
    }
  }
}
.gpt3__CTA-text{
  h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
  }
  p{
    font-family: var(--font-family);
    color: #0E0E0E;
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
  }
}
.gpt3__CTA-button{
  input{
    width: 189.14px;
    height: 59.4px;
    font-family: var(--font-family);
    color: #fff;
    background-color: #000;
    border-radius: 40px;
    border: none;
    outline: none;
    cursor: pointer;
  }
}