* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  text-decoration: none;
  color: unset;
}

.gradient__BG {
  /* ff 3.6+ */
  background: -moz-radial-gradient(circle at 3% 24%, rgb(0, 40, 83) 0%, rgb(4, 12, 24) 100%);
  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(circle at 3% 24%, rgb(0, 40, 83) 0%, rgb(4, 12, 24) 100%);
  /* opera 11.10+ */
  background: -o-radial-gradient(circle at 3% 24%, rgb(0, 40, 83) 0%, rgb(4, 12, 24) 100%);
  /* ie 10+ */
  background: -ms-radial-gradient(circle at 3% 24%, rgb(0, 40, 83) 0%, rgb(4, 12, 24) 100%);
  /* global 92%+ browsers support */
  background: radial-gradient(circle at 3% 24%, rgb(0, 40, 83) 0%, rgb(4, 12, 24) 100%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}
@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }
}
@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }
}

.section__margin {
  margin: 4rem 6rem;
}
@media screen and (max-width: 700px) {
  .section__margin {
    margin: 4rem;
  }
}
@media screen and (max-width: 550px) {
  .section__margin {
    margin: 4rem 2rem;
  }
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

