.gpt3__blog-header {
  margin-bottom: 7rem;
}
.gpt3__blog-header h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  max-width: 700px;
}

.gpt3__blog__content {
  display: flex;
}
@media screen and (max-width: 1000px) {
  .gpt3__blog__content {
    flex-direction: column-reverse;
  }
}

.gpt3__blog__content-groupA {
  flex: 1.5;
  margin-right: 3rem;
}
@media screen and (max-width: 1000px) {
  .gpt3__blog__content-groupA {
    margin-top: 3rem;
  }
}
@media screen and (max-width: 500px) {
  .gpt3__blog__content-groupA {
    margin-top: 1rem;
  }
}

.gpt3__blog__content-groupB {
  flex: 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
}
@media screen and (max-width: 500px) {
  .gpt3__blog__content-groupB {
    gap: 1rem;
  }
}

